@media (max-width: 1023px) {
  html {
    font-size: 1.25vw;
  }

  .img {
    background: url(../images/jarvimaisema2.webp);
    background-position: center;
    background-size: cover;
  }

  .content {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .topShade {
    width: 100%;
    margin-left: 0rem;
    position: fixed;
    opacity: 0.85;
    z-index: -1;
    height: 100rem;
    background-image: linear-gradient(to bottom, rgba(0, 125, 192, 1) 0%, rgba(0, 125, 192, 0.2) 30%, rgba(0, 125, 192, 0) 40%);
  }

  .title1 {
    display: none;
  }

  .title1-mobile {
    padding: 0;
    width: calc(100% + 4rem);
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 8.5rem;
    letter-spacing: 1rem;
    color: #ebebeb;
    text-shadow: 0.4rem 0.4rem 1rem rgb(0, 0, 0);
    height: 32rem;
    margin-top: 0rem;
    padding-top: 5rem;
    display: block;
    text-align: center;
  }

  .newEntryAndStatisticsContainer {
    font-size: 4rem;
    width: 100%;
    padding-left: 3rem;
    padding-right: 3rem;
  }

  .newEntryContainer,
  .barChartHoursMobileContainer,
  .countUpContainer,
  .radioGroupContainer,
  .barChartRankingsMobileContainer,
  .doughnutChartSpeciesMobileContainer {
    box-shadow: 1rem 1rem 2rem rgba(0, 0, 0, 0.3);
    border-radius: 2rem;
    backdrop-filter: blur(5rem);
    background-color: rgba(0, 0, 0, 0.5);
  }

  .newEntryContainer {
    width: 100%;
    margin-bottom: 8rem;
    margin-top: 0rem;
    padding: 5rem;
    padding-top: 4rem;
    padding-bottom: 4rem;

  }

  .title2 {
    font-size: 5rem;
    margin-bottom: 2rem;
    text-shadow: rgba(0, 0, 0, 0.6) 0.3rem 0.3rem 0.9rem;
    color: #eeeeee;
  }

  .formContainer {
    line-height: 8rem;
    padding: 0 0.3rem;
  }

  .entryForm input {
    margin-left: 0.8rem;
    font-size: 4rem;
    border-radius: .7rem;
    padding: 0.7rem;
    border-width: 0.25rem;
    width: 43rem;
  }

  input#locationCheckbox {
    width: 5rem;
    height: 5rem;
    margin: 2rem;
    margin-right: 0;
    margin-top: 2.5rem;
    transform: translate(0, 1.3rem);
  }

  .statisticsContainer1 {
    width: 100%;
    margin: 0;
    margin-bottom: 6rem;
  }

  .statisticsContainer2 {
    flex-direction: column;
    width: 100%;
    margin: 0;
    margin-bottom: 6rem;
  }

  .statisticsContainer3 {
    margin: 0;
    margin-bottom: 8rem;
    width: 100%;
  }

  .countUpContainer {
    padding-top: 5.92rem;
    width: 100%;
  }

  .countUpContainer #countUpTitle {
    font-size: 4rem;
    transform: translateY(-14.875rem);
  }

  .countUpContainer #countUp {
    font-size: 25rem;
    -webkit-text-stroke: 0.175rem black;
  }

  .barChartRankingsContainer {
    display: none;
  }

  .barChartRankingsMobileContainer {
    display: block;
    padding-top: 4.92rem;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 4rem;
    width: 100%;
    height: 110rem;
    margin-bottom: 6rem;
  }

  .doughnutChartSpeciesContainer {
    display: none;
  }

  .doughnutChartSpeciesMobileContainer {
    display: block;
    width: 100%;
    height: 90rem;
    padding: 3rem;
    padding-bottom: 3.5rem;
    padding-top: 3.25rem;

  }

  .barChartHoursContainer {
    display: none;
  }

  .barChartHoursMobileContainer {
    display: block;
    margin: auto;
    width: 100%;
    height: 130rem;
    padding-left: 3rem;
    padding-top: 4.92rem;
    padding-right: 4rem;
    padding-bottom: 4rem;
  }

  .button {
    /* border:0.15rem solid #0b0e07;   */
    color: #ffffff;
    padding: 1.2rem 3rem;
    border-radius: 1rem;
  }

  .button:hover {
    background-color: var(--theme-color);
  }

  .button:active {
    background-color: #0073d8;
    position: relative;
    top: 0.1875rem;
  }

  #submitButton {
    margin-top: 2.5rem;
    width: 43rem;
    font-size: 4rem;
    font-weight: bold;
  }

  #showStatsButton,
  #showSortingButton {
    margin-bottom: 1rem;
    width: max-content;
    font-size: 2.5rem;
    font-weight: bold;
  }

  #showSortingButton {
    margin-right: 1.3rem;
  }

  .radioGroupContainer {
    border-radius: 1rem;
    box-shadow: 1rem 0.2rem 2rem 0 rgba(0, 0, 0, 0.2);
    height: 6rem;
  }

  .radioGroupContainer2 {
    padding: 2rem;
    padding-left: 4rem;
  }

  .radioContainer {
    font-weight: bold;
    font-size: 2rem;
  }

  .checkmark {
    top: 0.5rem;
  }

  .tableContainer {
    width: fit-content;
    max-width: 100%;
    margin: auto;
    font-size: 1.75rem;
    margin-bottom: 82vh;
  }

  .entries {
    backdrop-filter: blur(5rem);
    font-family: Arial, Helvetica, sans-serif;
  }

  .entries td {
    border-bottom: 0.1rem solid rgb(89, 89, 89);
    border-top: 0.1rem solid rgb(89, 89, 89);
  }

  #deleteButton,
  #editButton {
    font-size: 1.2rem;
    border-width: 0.1rem;
  }

  #editButton {
    padding: 0.4rem 0rem;
    padding-left: 0.35rem;
  }

  #cancelEditButton {
    padding: 1.2rem 0rem;

  }

  .activePaginationButton {
    font-size: 2rem;
    padding: 1rem 2.5rem;
    background-color: #0073d8;
  }

  .inactivePaginationButton {
    font-size: 2rem;
    padding: 1rem 2.5rem;
  }

  .activePaginationButton:hover {
    background-color: #0073d8;
  }

  footer {
    font-size: 2rem;
    margin-bottom: 18vh;
  }

}

@media (display-mode: standalone) and (max-width: 750px) {
  .title1-mobile {
    margin-top: 8rem;
  }

  .newEntryContainer {
    margin-bottom: 10rem;
    margin-top: 8rem;
  }
}